.search {
  display: none;
  width: 100%;
  height: 180px;
  left: 0;
  top: 0;
  background-color: #fcf9ee;
  position: absolute;
}
.search .logo {
  width: 200px;
  height: auto;
  padding-top: 10px;
  margin: 0 45px;
  font-family: "Bookk Myungjo";
  font-weight: 700;
  font-style: bold;
}
.search .logo p {
  text-align: center;
  font-size: 30px;
  letter-spacing: 5px;
}
.search .logo p:last-child {
  font-size: 12px;
  letter-spacing: 5px;
}
.search input {
  border: none;
  border-bottom: 1px solid #000;
  display: block;
  width: 80%;
  margin: 30px auto;
  background-color: #fcf9ee;
}
.search input:focus {
  outline: none;
}
.search input::-webkit-search-decoration,
.search input::-webkit-search-cancel-button,
.search input::-webkit-search-results-button,
.search input::-webkit-search-results-decoration {
  opacity: 0;
  position: absolute;
  right: 0;
}
.search span {
  position: absolute;
  top: 0;
  right: 1%;
  cursor: pointer;
  font-size: 30px;
}

